import React, { Component } from 'react';
import PropTypes from 'prop-types'
import * as RecordsAPI from '../utils/RecordsAPI'

export default class Record extends Component {
  constructor() {
    super();
    this.state = {
      edit: false
    };
  }
  handleTogglle() {
    this.setState({
      edit: !this.state.edit
    })
  }
  handleEdit(e) {
    e.preventDefault()
    const record = {
      date: this.refs.date.value,
      title: this.refs.title.value,
      amount: Number.parseInt(this.refs.amount.value,0),
    }
    RecordsAPI.update(this.props.record.id, record).then(
      res => {
        this.setState({
          edit: false
        })
        this.props.handleEditRecord(this.props.record, res.data)
      }
    ).catch(
      error => {

      }
    )
  }
  handleDelete(e) {
    e.preventDefault()    
    RecordsAPI.remove(this.props.record.id).then(
      res => {
        this.props.handelDeleteRecord(this.props.record)
      }
    ).catch(
      error => console.log(error.message)
    )
  }
  recordRow() {
    return (
      <tr>
        <td>{this.props.record.date}</td>
        <td>{this.props.record.title}</td>
        <td>{this.props.record.amount}</td>
        <th>
          <button className="btn btn-info mr-1" onClick={this.handleTogglle.bind(this)}>Edit</button>
          <button className="btn btn-danger mr-1" onClick={this.handleDelete.bind(this)}>Delete</button>
        </th>
      </tr>
    );
  }
  recordForm() {
    return (
      <tr>
        <td><input type="text" className="form-control" defaultValue={this.props.record.date} ref="date"/></td>
        <td><input type="text" className="form-control" defaultValue={this.props.record.title} ref="title"/></td>
        <td><input type="text" className="form-control" defaultValue={this.props.record.amount} ref="amount"/></td>
        <th>
          <button className="btn btn-info mr-1" onClick={this.handleEdit.bind(this)}>Updata</button>
          <button className="btn btn-danger mr-1" onClick={this.handleTogglle.bind(this)}>Cancel</button>
        </th>
      </tr>
    );
  }
  render() {
    if (this.state.edit) {
      return this.recordForm();      
    } else {
      return this.recordRow();
    }
  }
}

Record.propTypes = {
  id: PropTypes.string,
  date: PropTypes.string,
  title: PropTypes.string,
  amount: PropTypes.number
}



